.button {
  display: inline-block;
  padding: 8px 16px;
  border-radius: 16px;
  max-width: 200px;
  border: 1px solid transparent;
  outline: none;
  font-size: 16px;
  line-height: 20px;
}

.button-accent {
  background-color: var(--lm-accent-color);
}

.button-icon {
  background: transparent;
  border-radius: 50%;
}
